import BuildingCard from '@/components/minecraft/BuildingCard';
import { getBuildings, getUserSession } from '@/app/actions';
import { Separator } from '@/components/ui/separator';

export const dynamic = 'force-dynamic'; // Ensure data is fetched on each request due to voting

export default async function HomePage() {
  const buildings = await getBuildings();
  const currentUser = await getUserSession();

  return (
    <div className="space-y-8">
      <div className="text-center space-y-2">
        <h2 className="text-4xl font-extrabold tracking-tight text-primary">建筑作品展示</h2>
        <p className="text-xl text-muted-foreground">
          探索令人惊叹的像素艺术，为你最喜爱的作品投票！
        </p>
      </div>
      <Separator className="my-6" />
      {buildings.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {buildings.map((building) => (
            <BuildingCard key={building.id} building={building} currentUser={currentUser} />
          ))}
        </div>
      ) : (
        <p className="text-center text-muted-foreground text-lg py-10">
          暂无建筑作品。敬请期待！
        </p>
      )}
    </div>
  );
}
